<script>
import {
  BCard,
  BAvatar,
  BForm,
  BFormInput,
  BRow,
  BCol,
  BFormSelect,
  BFormCheckbox,
  BButton,
  BSpinner,
  BCardText,
  BCollapse,
  BOverlay
} from "bootstrap-vue";
import axios from "@/libs/axios";
import {
  generateBgForBox,
} from "@/@core/utils";
import SearchInput from "@/@core/components/search-input/SearchInput.vue";
import Modal from '@core/components/Modal.vue'
import OtherChargeApis from '@/services/apis/OtherCharge'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

const debtList = new OtherChargeApis()

export default {
  components: {
    BCard,
    BAvatar,
    BForm,
    BFormInput,
    BRow,
    BCol,
    BFormSelect,
    BFormCheckbox,
    BButton,
    SearchInput,
    BSpinner,
    BCardText,
    BCollapse,
    Modal,
    BOverlay
  },
  data() {
    return {
      deletePending: false,
      showLoading: false,
      showDeleteModal: false,
      loading: false,
      pageData: [],
      show: false,
      searchInput: '',
      unit_debt: {}
    };
  },
  computed: {
    listData() {
      if (this.searchInput !== '') {
        return this.pageData.debtor_list.list.filter(item => 
          item.title.includes(this.searchInput) ||
          item.debt_name.includes(this.searchInput)
        )
      }
      return this.pageData.debtor_list?.list
    }
  },
  async created() {
await this.loadData()
  },
  methods: {
    generateBgForBox,
    async ContinueDeleting() {
      this.deletePending = true
      const deletedata = { unit_debt_id: this.unit_debt.unit_debt_id }
      await debtList.debtListDelete(deletedata).then(res => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: res.data.message,
            icon: 'EditIcon',
            variant: 'success',
          }
        })
        this.deletePending = false
        this.showDeleteModal = false
        this.listData = this.listData.filter(item => item.unit_debt_id != this.unit_debt_id)
        this.loadData()
      })
    },
    async loadData() {
      try {
      this.showLoading = true
      this.loading = true;
      const res = await axios.get('manager/debt/etc', {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      })
      this.pageData = res.data

      // console.log(res.data)
      this.loading = false;
      this.showLoading = false
    } catch (err) {
      this.loading = false;
      console.log(err)
    }
    },
    showDeleteModalfunc(item) {
      this.unit_debt = item
      this.showDeleteModal = true
    },
    edit(item) {
      this.$router.push({
        path: '/debt-registration/other/update',
        query: {
          id: item.unit_debt_id,
          debt_name: item.debt_name,
          title: item.title,
          debt_fee: item.debt_fee,
          locale_debt_fee: item.locale_debt_fee,
          unit_id: item.unit_id,
          debtor_id: item.debtor_id
        }
      })
    }
  }
};
</script>

